<template>
	<div class="offers-list">
		<v-container class="px-0 mt-3" grid-list-md>
			<v-layout wrap>
				<v-flex v-for="offer in offers" :key="offer.id" lg3 md4 sm6 xs12>
					<offers-list-item
						:offer="offer"
						:cart="cartIds.includes(offer.id)"
						:favorites="favoritesIds.includes(offer.id)"
					></offers-list-item>
				</v-flex>
			</v-layout>
		</v-container>
	</div>
</template>

<script>
import OffersListItem from "../components/OffersListItem.vue";
export default {
	components: {
		"offers-list-item": OffersListItem
	},
	created() {
		console.log(this);
	},
	computed: {
		offers() {
			return this.$store.state.offers.active;
		},
		cartIds() {
			return [1, 2, 3];
		},
		favoritesIds() {
			return [1, 2, 3];
		}
	}
};
</script>
